
.top-color-bar {
  background-image: -webkit-gradient(linear, left top, right top, from(red),color-stop(orange),color-stop(yellow),color-stop(green),color-stop(blue),color-stop(indigo),to(violet));
  background-image: -webkit-linear-gradient(left, red,orange,yellow,green,blue,indigo,violet);
  background-image: -o-linear-gradient(left, red,orange,yellow,green,blue,indigo,violet);
  background-image: linear-gradient(to right, red,orange,yellow,green,blue,indigo,violet); 
  /* background-image: linear-gradient(to right, orange, yellow, green, cyan, blue, violet); */
  /* background-image: linear-gradient(to right, #eea2a2 0%, #bbc1bf 19%, #57c6e1 42%, #b49fda 79%, #7ac5d8 100%); */
  /* background-image: linear-gradient(60deg, #abecd6 0%, #fbed96 100%); */
  height: 2px;
  position: relative;
  width: 100%;
}


